*,
*::after,
*::before {
    box-sizing: border-box;
}

ul {
  list-style: none;
}

a {
  color          : inherit;
  text-decoration: none;
}

body {
  color      : #000;
  font-size  : 18px;
  line-height: 21px;
}

.container {
  max-width: 1230px;
  margin   : 0 auto;
  padding  : 0 15px;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
  border-radius: 50%;
}

/* @media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
} */

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
  text-decoration: none;
}

